.left-bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: sticky;
  top: 0;
  left: 0;
}

.left-bar-items {
  display: flex;
  flex-direction: column;
  width: 12rem;
  gap: 1.3rem;
  margin-top: 5rem;
  overflow: scroll;
  height: calc(8 * (4.5rem + 1.3rem));
}

.left-bar-items::-webkit-scrollbar {
  display: none; /* Kaydırma çubuğunu gizle */
}

.left-bar-item {
  display: flex;
  width: 100%;
  padding: 0 0.6rem;
  min-height: 4.5rem;
  align-items: center;
  justify-content: center;
  background: hsl(0deg 0% 100% / 16%);
  border-radius: 0.25rem;
  cursor: pointer;
}

.left-bar-item-title {
  box-sizing: border-box;
  color: white;
  font-size: 0.9rem;
  font-weight: 700;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.active {
  background: #ff0143;
}

.arrow,
.arrow::before {
  position: absolute;
  left: 50%;
}

.arrow {
  width: 40px;
  height: 40px;
  border-left: none;
  border-top: none;
  border-right: 2px #fff solid;
  border-bottom: 2px #fff solid;
  transform: rotate(-135deg);
  margin: -10px 0 0 -10px;
}

.arrow:before {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  margin: -10px 0 0 -10px;
  border-left: none;
  border-top: none;
  top: 50%;
  border-right: 1px #fff solid;
  border-bottom: 1px #fff solid;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-name: arrow;
}

@keyframes arrow {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translate(-10px, -10px);
  }
}
