.dt-container {
  width: 95%;
}

.dt-content {
  margin-bottom: 4rem;
}

.dt-cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-top: 1rem;
  gap: 2rem;
}

.dt-card {
  width: 100%;
  min-width: 20rem;
  max-width: 25rem;
  aspect-ratio: 16/11;
  background: #ff0143;
  display: grid;
  grid-template-rows: 4fr 1fr;
  border-radius: 0.5rem;
  overflow: hidden;
  cursor: pointer;
}

.dt-card-img {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  background: #232428;
  padding: 0.2rem;
  text-align: center;
}

.dt-card-img img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0.6;
}

.dt-card-img h2 {
  position: absolute;
  color: white;
  z-index: 1;
  padding: 1rem;
  width: 100%;
}

.dt-card-header {
  display: flex;
  padding: 0.8rem 1rem;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  color: white;
  min-height: min-content;
}

.dt-card-header-details {
  display: flex;
  justify-content: center;
  gap: 0.6rem;
}

.dt-card-header-details-item {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  width: max-content;
  font-size: 0.9rem;
  color: white;
}

.dt-card-details {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  width: 80%;
  max-width: 90rem;
  height: 50rem;
  max-height: 80%;
  background: #232428;
  border-radius: 0.5rem;
  padding: 2rem 4rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  overflow-y: auto;
  color: white;
  overflow-y: scroll;
  gap: 2rem;
}

.dt-card-details::-webkit-scrollbar {
  display: none;
}

.dt-card-details .dt-card-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.dt-card-details-content {
  display: grid;
  grid-template-columns: 4fr 1fr;
  width: 100%;
  height: 100%;
  gap: 1rem;
}

.dt-card-video {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  aspect-ratio: 16/9;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  overflow: hidden;
}

.dt-card-video-content {
  display: flex;
  flex-direction: column;
  width: 100%;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 0.2rem;
  overflow: scroll;
  color: #232428;
}

.dt-card-video-content::-webkit-scrollbar {
  display: none;
}

.dt-card-video-detail {
  display: grid;
  grid-template-rows: 2fr 1fr;
  padding: 1rem;
  min-height: 4rem;
  color: white;
  cursor: pointer;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

/* Responsive */

@media screen and (max-width: 1700px) {
  .dt-cards {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media screen and (max-width: 1400px) {
  .dt-cards {
    grid-template-columns: repeat(2, 1fr);
  }
  .dt-card-details-content {
    display: flex;
    flex-direction: column;
  }
}

@media screen and (max-width: 1100px) {
  .dt-cards {
    grid-template-columns: 1fr;
  }
}

