nav {
  display: flex;
  justify-content: center;
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  height: 4rem;
  background: transparent;
  z-index: 11;
  transition: all 200ms ease;
}

.navbar {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 90%;
  align-items: center;
}

.window-scroll {
  background: #0b0b0b;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}

.nav-left {
  display: flex;
  align-items: baseline;
  gap: 2rem;
}

.nav-menu {
  display: flex;

  gap: 1rem;
}
